import { DefaultTheme, createGlobalStyle, css } from "styled-components";
import reset from "styled-reset";

export const theme: DefaultTheme = {
  font: "Open Sans, sans-serif",

  aboutFont: "Dosis",

  colors: {
    transparent: "transparent",
    light: {
      50: "#f9f9f9",
      100: "#f2f2f2",
      200: "#e9e9e9",
      300: "#d9d9d9",
      400: "#b5b5b5",
      500: "#959595",
      600: "#6d6d6d",
      700: "#5a5a5a",
      800: "#3b3b3b",
      900: "#1b1b1b",
    },
    dark: {
      50: "#F7F7F7",
      100: "#EEEEEE",
      200: "#E2E2E2",
      300: "#D0D0D0",
      400: "#ABABAB",
      500: "#8A8A8A",
      600: "#636363",
      700: "#505050",
      800: "#323232",
      900: "#121212",
    },
  },
};

export const GlobalStyle = createGlobalStyle`
 ${reset}

 

  *, *:before, *:after {
    box-sizing: border-box;
  }

  html {
    font-family: "Open Sans, sans-serif";
  }

  a{
    text-decoration:none;
    color: var(--secondary);
     &:hover {
      color:  var(--hover);
     }
  }

  button{
    outline:none;
  }


  body {
    margin: 0;
    padding: 0;
    background-color: var(--primary);
    color: var(--secondary);

    &.light {
      --primary: ${({ theme }) => theme.colors.light[50]};
      --secondary: ${({ theme }) => theme.colors.dark[900]};
      --surface: ${({ theme }) => theme.colors.light[100]};
      --hover:${({ theme }) => theme.colors.dark[600]}
    }

    &.dark {
      --primary: ${({ theme }) => theme.colors.dark[900]};
      --secondary: ${({ theme }) => theme.colors.light[100]};
      --surface: ${({ theme }) => theme.colors.dark[900]};
      --background: var(--primary);
      --hover: ${({ theme }) => theme.colors.light[400]};
    }
   
  }

`;
